$myFont: Helvetica, sans-serif;
$myPrimary: red;
$mySecondary: green;
$myWidth: 80%;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

button {
  background-color: $myPrimary;
  &:hover {
    background-color: $mySecondary;
    filter: brightness(80%);
    transition: 0.5s ease-in-out;
    box-shadow: 0px 0px 3px;
  }
}
